.main {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  overflow: hidden;
}
.main > img {
  position: absolute;
  z-index: -1;
  top: 0px;
}
.header {
  position: relative;
}
.header h2 {
  font-size: 30px;
  font-weight: 800;
  color: hsl(227, 35%, 25%);
}
.header p {
  color: hsl(225, 20%, 60%);
  margin-top: 10px;
}
.header img {
  position: absolute;
  width: 160px;
  bottom: -36px;
  left: 31%;
  z-index: -1;
}
.container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  width: 600px;
  height: 400px;
  border-radius: 10px;
  box-shadow: 0px 0px 50px 0px hsl(222, 53%, 83%);
  margin-bottom: 190px;
  background-color: hsl(230, 100%, 99%);
}
.container-Header {
  width: 80%;
}
.container-Header-Text {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
.container-Header-Text > p {
  text-transform: uppercase;
  color: hsl(225, 20%, 60%);
  font-weight: 700;
  font-size: 14px;
  letter-spacing: 2px;
}
.container-Price {
  display: flex;
  justify-content: center;
  align-items: center;
  color: hsl(225, 20%, 60%);
  font-weight: 600;
}
.container-Price h1 {
  color: hsl(227, 35%, 25%);
  font-size: 46px;
  font-weight: 800;
  margin-right: 5px;
}
.slider-container {
  position: relative;
  width: 100%;
  margin: 0px auto;
}
.Header-slider {
  position: relative;
  -webkit-appearance: none;
  width: 100%;
  height: 10px;
  border-radius: 10px;
  background: transparent;
  z-index: 3;
}
.Header-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 40px;
  height: 40px;
  border: 0;
  background: url("./../images/icon-slider.svg");
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  background-color: hsl(174, 86%, 45%);
  border-radius: 50%;
  box-shadow: 0px 12px 20px 5px #00ffff85;
}
.Header-slider::-webkit-slider-thumb:active {
  background-color: hsl(174deg 86% 36%);
}
.Header-Bar-Fill-Lower {
  content: "";
  position: absolute;
  background-color: hsl(174, 77%, 80%);
  width: 0%;
  height: 10px;
  top: 8px;
  z-index: 2;
  border-radius: 5px;
}
.Header-Bar-Fill-Higher {
  content: "";
  position: absolute;
  background-color: hsl(224, 65%, 95%);
  width: 100%;
  height: 10px;
  top: 8px;
  z-index: 1;
  border-radius: 5px;
}
.subscriptions-type {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  color: hsl(225, 20%, 60%);
  font-weight: 600;
  width: 80%;
  font-size: 14px;
}
.switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 24px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: hsl(223, 50%, 87%);
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: hsl(174, 86%, 45%);
}

input:focus + .slider {
  box-shadow: 0 0 1px hsl(174, 86%, 45%);
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
.discount {
  display: flex;
  justify-content: center;
  align-items: center;
}
.discount-offer {
  display: flex;
  margin-left: 10px;
  margin-top: 3px;
  background-color: hsl(14, 92%, 95%);
  border-radius: 20px;
  color: hsl(15, 100%, 70%);
  padding: 4px 6px;
  font-weight: 800;
  box-sizing: border-box;
  font-size: 10px;
}
.trial-container {
  width: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.subs-benefits {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.benefit {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 180px;
  margin-bottom: 5px;
}
.benefit p {
  width: 155px;
  text-align: left;
  font-size: 14px;
  font-weight: 600;
  color: hsl(225, 20%, 60%);
}
.text-fix {
  width: 156px !important;
}
.trial-container button {
  cursor: pointer;
  outline: none;
  border: none;
  color: hsl(226, 100%, 87%);
  font-weight: 800;
  width: 150px;
  height: 40px;
  border-radius: 20px;
  background-color: hsl(227, 35%, 25%);
}
@media screen and (max-width: 630px) {
  .container {
    width: 500px;
  }
}
@media screen and (max-width: 530px) {
  .container {
    width: 400px;
  }
  .discount-offer p:nth-of-type(2) {
    display: none;
  }
}
@media screen and (max-width: 420px) {
  .container {
    width: 90%; height: 60%;
    margin: 0px;
    justify-content: space-around;
  }
  .container-Header {
    width: 90%;
  }
  .container-Header-Text {
    width: 100%;
    flex-direction: column;
  }
  .subscriptions-type {
    width: 100%;
  }
  .trial-container {
    width: 90%;
  }
}